footer {
    background-color: #172146;
    color: #fff;
    display: flex;
    justify-content: center;
    padding-top: 80px;
    padding-bottom: 80px;
    margin-top: 40px;
}
.link-wrap {
    max-width: 250px;
    margin-left: 90px;
    margin-right: 90px;
}
.link-wrap p {
    margin-top: 6px;
    margin-bottom: 6px;
    font-size: 14px;
    cursor: pointer;
 }
 
.link-wrap h4 {
    color: rgba(255, 255, 255, 0.637);
    margin-top: 15px;
    margin-bottom: 15px;
}
.footer-img {
    max-width: 20px;
    max-height: 20px;
    margin-right: 20px;
}
#icons , #mail {
    margin-top: 15px;
    margin-bottom: 15px;
}