nav {
    display:flex;
    align-items:center;
    padding: 13px 78px;
    background-color:#fff;
    box-shadow: 0 0 4px black;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2000;
}
nav div:nth-of-type(1) {
    text-align: left;
    line-height: 23px;
}
#logo {
    width:130px;
    font-weight: 900;
    font-size:32px;
    color:#0084ff;
    margin:0;
    padding:0;
 }
 #logo-stories-text {
    margin:0;
    padding:0;
   font-weight:400;
   font-style:italic;
   font-size:18px;
 }
 #dropdown-menu {
   cursor: pointer;
   color:#000;
 }
 #show-dropdown {
   margin-left: 60px;
 }
 #dropdown-icon {
   display:inline-block;
   padding-left:8px;
   padding-top:0px;
  }
 #dropdown {
   position:absolute;
   width:220px;
   height:auto;
   font-size:14px;
   font-weight:600;
   letter-spacing:1px;
   border-radius: 4px;
   background-color:#fff;
   box-sizing:border-box;
   box-shadow: 0 1px 8px rgb(0 0 0 / 50%);
   padding-left:20px;
  
  }
  .display-none {
    display:none;
  }
  #dropdown p a {
    text-decoration:none;
    color:#666;
  }
  #disclamer {
    margin: 0;
    margin-left: 35%;
    max-width: 300px;
    color: rgb(161, 161, 161);
    line-height: 18px;
  }
  #btn {
    background: linear-gradient(
      269deg
      ,#0048ff,#0096ff);
   color: #fff;
   padding: 13px 20px;
   font-size: 13px;
   word-spacing: 0px;
   font-weight: bold;
   border-radius: 25px;
   margin-left: 2%;
   cursor: pointer;
  }
  #btn:hover {
    transform: scale(1.03);
  }