
  .card {
    width:33.3%;
    max-width:20.5em;
    max-height: 29em;
    box-shadow: 0 5px 20px 0 rgba(0,0,0,20%);
    border: 1px solid rgba(0,168,255,.3);
    border-radius: 5px;
    margin-left: 1%;
    margin-right:1%;
    margin-bottom: 1em;
    cursor: pointer;
}
.card:hover {
   transform: translateY(-5px);
   box-shadow: 0 5px 20px 0 rgba(0,0,0,70%);
}
  .card-img {
    height:176px;
    width:100%;
  }
  .card-img img {
    width:100%;
    height:100%;
    border-top-left-radius:5px;
    border-top-right-radius:5px;
  }
  .text-section {
    padding:10px 24px;
   
  }
  .title {
    font-size: 18px;
    text-align: left;
    margin: 0;
    font-weight: 600;
    max-height: 50px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .author-section {
    padding-top: 4px;
    font-size: 14px;
    color: rgba(0,0,0,0.7);
    font-weight: 300;
   }
  .author {
    text-align: left;
    font-size:14px;
    font-style: italic;
    color: #0096ff;
    font-weight:500;
  }
  .details-text {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.43;
    color: rgba(0,0,0,0.6);
    margin: 4px 0px;
    overflow: hidden;
    text-overflow: ellipsis !important;
    max-height: 100px;
  }