#categories-container {
    font-size:16px;
    padding-top:16px;
    margin-bottom:20px;
    width:80%;
    margin-left:auto;
    margin-right:auto;
    margin-top: 80px;
  }
  #heading {
    font-weight:600;
    color:#000;
    margin:0;
    font-size:26px;
  }
  #filter {
    margin-top:15px;
    display:flex;
    align-items:center;
  }
  #filter-img {
    width:25px;
    height:25px; 
  }
  #filter-img img {
    width:100%;
  }
  #filter-text {
    font-size:14px;
    margin-left: 8px;
    font-weight:600;
  }
  .categories {
    display:inline-block;
    width:auto;
    padding:6px 14px;
    color:#0084ff;
    border-radius:15px;
    border:1px solid #0084ff;
    margin:5px;
    cursor:pointer;
  }
  .categories:active {
    color:#fff;
    background-color:#0084ff;
    }
  .active {
    color:#fff;
    background-color:#0084ff;
  } 