@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Raleway', sans-serif;
  background-color:#f2f6ff;
  margin:0;
  padding:0;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
#cards {
  width:70%;
  margin:auto;
  text-align: left;
  display:flex;
  flex-wrap:wrap;
  flex-direction:row;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

nav {
    display:flex;
    align-items:center;
    padding: 13px 78px;
    background-color:#fff;
    box-shadow: 0 0 4px black;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2000;
}
nav div:nth-of-type(1) {
    text-align: left;
    line-height: 23px;
}
#logo {
    width:130px;
    font-weight: 900;
    font-size:32px;
    color:#0084ff;
    margin:0;
    padding:0;
 }
 #logo-stories-text {
    margin:0;
    padding:0;
   font-weight:400;
   font-style:italic;
   font-size:18px;
 }
 #dropdown-menu {
   cursor: pointer;
   color:#000;
 }
 #show-dropdown {
   margin-left: 60px;
 }
 #dropdown-icon {
   display:inline-block;
   padding-left:8px;
   padding-top:0px;
  }
 #dropdown {
   position:absolute;
   width:220px;
   height:auto;
   font-size:14px;
   font-weight:600;
   letter-spacing:1px;
   border-radius: 4px;
   background-color:#fff;
   box-sizing:border-box;
   box-shadow: 0 1px 8px rgb(0 0 0 / 50%);
   padding-left:20px;
  
  }
  .display-none {
    display:none;
  }
  #dropdown p a {
    text-decoration:none;
    color:#666;
  }
  #disclamer {
    margin: 0;
    margin-left: 35%;
    max-width: 300px;
    color: rgb(161, 161, 161);
    line-height: 18px;
  }
  #btn {
    background: linear-gradient(
      269deg
      ,#0048ff,#0096ff);
   color: #fff;
   padding: 13px 20px;
   font-size: 13px;
   word-spacing: 0px;
   font-weight: bold;
   border-radius: 25px;
   margin-left: 2%;
   cursor: pointer;
  }
  #btn:hover {
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
  }
#categories-container {
    font-size:16px;
    padding-top:16px;
    margin-bottom:20px;
    width:80%;
    margin-left:auto;
    margin-right:auto;
    margin-top: 80px;
  }
  #heading {
    font-weight:600;
    color:#000;
    margin:0;
    font-size:26px;
  }
  #filter {
    margin-top:15px;
    display:flex;
    align-items:center;
  }
  #filter-img {
    width:25px;
    height:25px; 
  }
  #filter-img img {
    width:100%;
  }
  #filter-text {
    font-size:14px;
    margin-left: 8px;
    font-weight:600;
  }
  .categories {
    display:inline-block;
    width:auto;
    padding:6px 14px;
    color:#0084ff;
    border-radius:15px;
    border:1px solid #0084ff;
    margin:5px;
    cursor:pointer;
  }
  .categories:active {
    color:#fff;
    background-color:#0084ff;
    }
  .active {
    color:#fff;
    background-color:#0084ff;
  } 

  .card {
    width:33.3%;
    max-width:20.5em;
    max-height: 29em;
    box-shadow: 0 5px 20px 0 rgba(0,0,0,20%);
    border: 1px solid rgba(0,168,255,.3);
    border-radius: 5px;
    margin-left: 1%;
    margin-right:1%;
    margin-bottom: 1em;
    cursor: pointer;
}
.card:hover {
   -webkit-transform: translateY(-5px);
           transform: translateY(-5px);
   box-shadow: 0 5px 20px 0 rgba(0,0,0,70%);
}
  .card-img {
    height:176px;
    width:100%;
  }
  .card-img img {
    width:100%;
    height:100%;
    border-top-left-radius:5px;
    border-top-right-radius:5px;
  }
  .text-section {
    padding:10px 24px;
   
  }
  .title {
    font-size: 18px;
    text-align: left;
    margin: 0;
    font-weight: 600;
    max-height: 50px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .author-section {
    padding-top: 4px;
    font-size: 14px;
    color: rgba(0,0,0,0.7);
    font-weight: 300;
   }
  .author {
    text-align: left;
    font-size:14px;
    font-style: italic;
    color: #0096ff;
    font-weight:500;
  }
  .details-text {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.43;
    color: rgba(0,0,0,0.6);
    margin: 4px 0px;
    overflow: hidden;
    text-overflow: ellipsis !important;
    max-height: 100px;
  }
footer {
    background-color: #172146;
    color: #fff;
    display: flex;
    justify-content: center;
    padding-top: 80px;
    padding-bottom: 80px;
    margin-top: 40px;
}
.link-wrap {
    max-width: 250px;
    margin-left: 90px;
    margin-right: 90px;
}
.link-wrap p {
    margin-top: 6px;
    margin-bottom: 6px;
    font-size: 14px;
    cursor: pointer;
 }
 
.link-wrap h4 {
    color: rgba(255, 255, 255, 0.637);
    margin-top: 15px;
    margin-bottom: 15px;
}
.footer-img {
    max-width: 20px;
    max-height: 20px;
    margin-right: 20px;
}
#icons , #mail {
    margin-top: 15px;
    margin-bottom: 15px;
}
